@import '_variables';
body {
	background-color: $color0;
	font-family: 'Montserrat', sans-serif;
	a{
		cursor: pointer;
	}
}
.navbar{
	height: $altonav;
	&.home, &.en{
		background-color: $color1;
	}
	&.servicios{
		background-color: $color2;
	}
	&.casos{
		background-color: $color7;
	}
	&.productos{
		background-color: $color1;
	}
	&.nosotros{
		background: transparent;
		transition: all .3s;
		&.bg{
			background-color: $color4;
		}
		@media(max-width: 767px){
			background-color: $color4;
		}
	}
	img{
		height: 25px;
	}

	.navbar-nav{
		&.es{
			transition: all .3s;
			/*margin-left: calc(50% - 266px); */
			margin-left: calc(50% - 160px);
			&.move{
				transition: all .3s;
				/*margin-left: calc(100% - 525px);*/
				margin-left: calc(100% - 300px);
			}
		}
		&.en{
			transition: all .3s;
			/*margin-left: calc(50% - 306px);*/
			margin-left: calc(50% - 160px);
			&.move{
				transition: all .3s;
				/*margin-left: calc(100% - 610px);*/
				margin-left: calc(100% - 300px);
			}
		}
		
		li{
			cursor: pointer;
			.nav-link{
				color: $color0;
				padding-right: 20px;
				padding-left: 20px;
				cursor: pointer;
				text-transform: lowercase;
				@media(max-width: 992px) and (min-width: 768px){
					/*font-size: 13px;*/
				}
			}
			&.active{
				.nav-link{
					&:after{
						display: block;
						content: '';
						border-bottom: 5px solid #fff;
						width: 100%;
						height: 4px;
					}
					
				}
			}
		}	
	}
	&.home, &.en{
		.navbar-brand{
			-webkit-transition: all .3s;
			transition: all .3s;
			animation-duration: .3s;
			margin-top: 0;
			margin-left: 0;
			display: none;
			&.visible{
				display: block;
			}
		}
	}
	&:not(.home){
		.navbar-nav{
			transition: none;
			margin-left: auto;
			float: right;
		}
	}
	@media(max-width: 767px){
		.navbar-toggler{
			position: absolute;
			right: 15px;
			top: 15px;
			box-shadow: none !important;
			outline: none !important;
			.navbar-toggler-icon{
				display: block;
			}
			.navbar-toggler-icon-cerrar{
				display: none;
			}
			&.active{
				border: none;
				.navbar-toggler-icon{
					display: none;
				}
				.navbar-toggler-icon-cerrar{
					display: block;
					svg{
						height: 30px;
						path{
							fill: #fff;
						}
						
					}
				}
			}
		}

		.navbar-nav{
			&.es, &.en, &:not(.home){
				margin-left: auto;
				width: 100%;
				border-radius: 0;
				padding: 10px 0 15px;
				float: none;

				li{
					a{
						color: $color8;
						&:hover, &:focus{
							color: $color5;
						}
					}
					&.active{
						a{
							color: $color8;
							font-weight: bold;
							&:after{
								display: none;
							}
							&:hover, &:focus{
								color: $color5;
							}
						} 
					}
				}
			}
		}
	}
}
.home-cuoma{
	background-color: $color1;
	height: 730px;
	text-align: center;
	color: $color0;
	transition: all .3s;
	position: relative;
	@media(max-width: 1199px){
		height: 500px;
	}
	@media(max-width: 992px){
		height: 380px;
	}
	h1{
		margin-top: 40px;
		font-weight: normal;
		font-size: 20px;
		@media(max-width: 992px){
			font-size: 18px;
			margin-top: 20px;
		}
	}
	.logo-home{
		animation-duration: .5s;
		transition: all .5s;
		height: 150px;
		width: auto;
		@media(max-width: 1199px){
			height: 100px;
		}
		@media(max-width: 992px){
			height: 70px;
		}
		@media(max-width: 767px){
			height: 30px;
		}
		&.move{
			transition: all .5s;
			height: 24px;
			margin-top: -40px;
		}
	}
	.btn-down{
		position: absolute;
		bottom: 20px;
		left: 50%;
		margin-left: -20px;
		svg{
			width: 40px;
			path{
				fill: $color10;
			}
		} 
		@media(max-width: 767px){
			display: none;
		}
	}
	/*.fade{
		transition: opacity .3s;
		}*/
		&.fadeOut{
			transition: all .3s;
			transform: scaleY(0.1);
			margin-top: -130px;
		}
	}
	.wrapper{
		transition: margin .3s;
		transition-delay: 0;
		margin: 0;
		&.align{
			margin-top: -150px;
			transition: margin .3s;
			transition-delay: .3s;
		}
	}
	.section{
		padding: 50px 0;
		@media(max-width: 992px){
			padding: 40px 0 30px;
		}
		@media(max-width: 767px){
			padding: 30px 0 20px;
		}
		.title{
			color: $color6;
			font-size: 25px;
			text-align: center;
			margin-bottom: 30px;
			strong{
				color: $color2;
			}
			@media(max-width: 767px){
				font-size: 20px;
			}
		}
		.grid-services{
			display: flex;
			width: 100%;
			max-width: 764px;
			margin: 30px auto;
			padding: 0;
			flex-wrap: wrap;
			border-radius: 4px;
			overflow: hidden;
			border: 1px solid $color6;
			li{
				width: 25%;
				height: 164px;
				list-style: none;
				border: 2px solid $color6;
				font-size: 14px;
				text-align: center;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				i{
					display:block;
					font-size: 60px;
					margin-bottom: 10px;
				}
				svg{
					display:block;
					margin: 0 auto 10px;
					fill: $color6;
					max-width: 32px;
					max-height: 42px;
				}
				span{
					font-weight: bold;
				}
				a{
					color: $color6;
					transition: all .3s;
					svg{
						fill: $color6;
						transition: all .3s;
					}
					&:hover, &:focus{
						color: $color1;
						text-decoration: none;
						svg{
							fill: $color1;
						}
					}
				}
			}
			@media(max-width: 767px){
				max-width:300px;
				li{
					width: 50%;
					height: 100px;
					font-size: 12px;
					i{
						font-size: 40px;
					}
				}
			}
		}
		.card-info{
			margin-bottom: 30px;
			cursor: pointer;

			.front{
				padding:20px;
				transition: all .3s;
				img{
					width: 100%;
					height: auto;
					margin-bottom: 10px;
				}
				h3{
					font-size: 16px;
					font-weight: 700;
					margin-bottom: 0;
					color: $color10;
				}
				p{
					font-size: 14px;
					color: $color6;
				}
				@media(max-width: 1199px){
					p{
						font-size: 12px;
					}
				}
				@media(max-width: 767px){
					min-height: 250px;
					h3{
						font-size: 14px;
					}
					p{
						font-size: 12px;
					}
				}
				&:hover{
				opacity: .7;
			}
			}
			.back{
				background-color: rgb(255, 255, 255);
				box-shadow: 0px 3px 40px 0px rgba(0, 0, 0, 0.2);
				min-height: 355px;
				padding: 20px;
				h3{
					font-size: 16px;
					font-weight: 700;
				}
				p{
					font-size: 14px;
				}
				@media(max-width: 1199px){
					p{
						font-size: 12px;
					}
				}
				@media(max-width: 767px){
					min-height: 300px;
					h3{
						font-size: 14px;
					}
					p{
						font-size: 12px;
					}
				}
			}
			
		}
		.brands{
			display: flex;
			flex-wrap: wrap;
			.item{
				width: 20%;
				margin-bottom: 20px;
				height: 100px;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				img{
					max-width: 80%;
					max-height: 70%;

				}
				@media(max-width: 992px){
					width: 33.3%;
				}
				@media(max-width: 767px){
					width: 50%;
					padding: 0 10px;
				}
			}

		}
		&.section-cuoma{
			p{
				text-align: center;
				max-width: 80%;
				margin: 0 auto 20px;
				font-size: 18px;
				color: $color5;
			}
			.team{
				display: flex;
				flex-wrap: wrap;
				padding-left: 0;
				@media(max-width: 767px){
					width: 300px;
					margin: 0 auto;
				}
				.card-team{
					margin-bottom: 0;
					cursor: pointer;
					width: 16.6%;
					height:184px;
					list-style: none;
					position: relative;
					@media(max-width: 1199px){
						height: 154px;
					}
					@media(max-width: 992px){
						height: 115px;
					}

					@media(max-width: 767px){
						height: 100px;
						width: 100px;
					}

					.front{
						position: absolute;
						padding:0;
						img{
							width: 100%;
							margin-bottom: 0;
							border: 1px solid #e7e7e7;
						}
					}
					.back{
						position: absolute;
						width: 100%;
						padding: 20px;
						height: 184px;
						background-color: rgba(255,255,255,.7);
						display: none;
						border: 4px solid $color6;
						text-align: center;
						h3{
							font-size: 16px;
							font-weight: 700;
							margin-bottom: 5px;
							color: $color6;
						}
						p{
							font-size: 14px;
						}
						a{
							position: absolute;
							bottom: 5px;
							left: 50%;
							margin-left: -10px;
							cursor: pointer;
							svg{
								width:20px;
								path{
									fill: $color6;
								}
							}
							&:hover, &:focus{
								opacity: .7;
							}
						}
						@media(max-width: 1199px){
							height: 154px;
							padding: 15px;
						}
						@media(max-width: 992px){
							height: 115px;
							padding: 10px 5px;
							h3{
								font-size: 13px;
								margin-bottom: 0;
							}
							p{
								font-size: 10px;
							}
							a{
								bottom: 0;
								margin-left: -8px;
								svg{
									width: 16px;
								}
							}
						}


						@media(max-width: 767px){
							height: 100px;
							width: 100px;
							padding-left: 5px;
							padding-right: 5px;
							h3{
								font-size: 12px;
								margin-bottom: 0;
							}
							p{
								font-size: 9px;
							}
							a{
								bottom: 0;
								svg{
									width: 14px;
								}
							}
						}

					}
					&:nth-child(7){
						margin-left: 8.3%;

						@media(max-width: 767px){
							margin-left: 0;
						}

					}
					&:nth-child(11){
						margin-right: 8.3%;
						@media(max-width: 767px){
							margin-right: 0;
						}
					}
					&:nth-child(12){
						margin-left: 16.6%;
						@media(max-width: 767px){
							margin-left: 0;
						}
					}
				}
			}
		}
	}
	footer{
		background-color: $color7;
		padding: 20px 0 0;
		color: $color4;
		font-size: 12px;
		@media(max-width: 767px){
			padding-top: 0;
		}
		h4{
			font-size: 14px;
			color: #fff;
			font-weight: bold;
			text-transform: uppercase;
			@media(max-width: 767px){
				margin-top: 20px;
			}
		}
		p{
			svg{
				display: inline-block;
				width: 14px;
				vertical-align: baseline;
				path{
					fill: $color4;
				}
			}
		}
		a{
			color: $color4;
			&:hover, &:focus{
				color: $color4;
				text-decoration: none;
				opacity:.8;
			}
		}
		.btn{
			border: 2px solid $color4;
			border-radius: 0;
			padding: 8px 5px 5px;
			width: 40px;
			height: 40px;
			svg{
				width:18px;
				path{
					fill: $color4;
				}
			}
		}
		.copy{
			padding: 10px 0;
			margin-top: 20px;
			background-color: $color8;
		}
		form {
			.form-group{
				margin-bottom: 10px;
				position: relative;
				.form-control{
					border: 2px solid $color4;
					border-radius: 0;
					padding: 8px 5px 5px;
					width: 100%;
					height: 40px;
					background: transparent;
					color: $color4;
					font-size: 12px;
					font-weight: normal;
					-webkit-transition: all .3s;
					transition: all .3s;
					&::placeholder{
						color: $color4;
					}
					&:focus{
						outline: none !important;
						box-shadow: none !important;
						background-color: $color4;
						color: $color8;
						&::placeholder{
							color: $color8;
						}
					}
					&.parsley-error{
						border-color: $color2;
					}
				}

				textarea.form-control{
					/*height: 90px;*/
					resize:  none;
					&:focus{
						height: 90px;
					}
				}
				.btn-send{
					background: transparent;
					border: none;
					position: absolute;
					top: 10px; 
					right: 5px;
					cursor: pointer;
					outline: none;
					svg{
						width:18px;
						path{
							fill: $color4;
						}
					}
					&.active{
						svg{
							width:18px;
							path{
								fill: $color7;
							}
						}
					}
				}
				ul{
					padding: 0;
					li{
						list-style: none;
						color: $color2;
					}
				}
			}

		}
		#ok{
			display: none;
			h5{
				font-size: 15px;
			}
		}
	}
	.section-contacto{
		height: 84vh;
		background: url(/assets/img/bg-contacto.jpg) no-repeat center center;
		background-size: cover;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		h1{
			font-size: 70px;
			font-weight: bold;
			color: #fff;
			strong{
				font-weight: bold;
				color: $color3;
			}
			@media(max-width: 992px){
				font-size: 54px;
			}
			@media(max-width: 767px){
				font-size: 36px;
				margin-top: 40px;
			}
		}
		@media(max-width: 767px){
			height: 300px;
			background-position: right -310px top -90px;
			background-size: 1300px auto;
		}
		@media(max-width: 320px){
			height: 280px;
			background-position: right -330px top -98px;
		}
	}


	.section-nosotros{
		height: 700px;
		width: 100%;
		@media(max-width:767px){
			background-color: $color4;
		}
		#bg-video {
			position: absolute;
			left: 0;
			top: 0;
			min-width: 100%; 
			z-index: -1;

			@media(max-width:992px){
				max-width: 100%;
			}
			@media(max-width:767px){
				display: none;
			}
		}
		.cont-nosotros{
			position: absolute;
			left: 0;
			top: 0;
			min-width: 100%; 
			height: 700px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}
		h1{
			font-size: 70px;
			font-weight: bold;
			color: #fff;
			strong{
				font-weight: bold;
				color: $color3;
			}
			@media(max-width: 1199px){
				font-size: 62px;
			}
			@media(max-width: 992px){
				font-size: 54px;
			}
			@media(max-width: 767px){
				font-size: 36px;
				margin-top: 30px;
			}
		}
		@media(max-width: 1199px){
			height: 500px;
			.cont-nosotros{
				height: 500px;
			}
		}
		@media(max-width: 992px){
			height: 450px;
			.cont-nosotros{
				height: 450px;
			}
		}
		@media(max-width: 767px){
			height: 300px;
			.cont-nosotros{
				height: 300px;
			}
		}
	}
	.wrapper-nosotros{
		background: #fff;
		padding: 40px 0;
		@media(max-width: 767px){
			padding: 15px 0;
		}
	}

	footer.contacto{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@media(max-width: 767px){
			position: static;
		}
	}
	.fixed-top{
		z-index: 999999;
	}


	.content{		
		height: 730px;
		text-align: center;
		transition: all .3s;
		position: relative;
		@media(max-width: 1199px){
			height: 500px;
		}
		@media(max-width: 992px){
			height: 380px;
		}
		h2{
			margin-top: 40px;
			font-weight: normal;
			font-size: 20px;
			@media(max-width: 992px){
