			}
		}
		&.servicios{
			background-color: $color2;
			color: $color0;
		}
		&.casos{
			background-color: $color7;
			color: $color0;
		}
		&.productos{
			background-color: $color1;
			color: $color0;
		}
		&.nosotros{
			color: $color0;
		}
	}	

	.banner-cuoma{
		background-color: $color2;
		padding: 100px 40px;
		text-align: center;
		h2{
			font-size: 70px;
			font-weight: bold;
			color: #fff;
			strong{
				font-weight: bold;
				color: $color3;
			}
			@media(max-width: 992px){
				font-size: 54px;
			}
			@media(max-width: 767px){
				font-size: 36px;
			}
		}
		@media(max-width:992px){
			padding: 50px 20px;
		}
		@media(max-width:767px){
			padding: 30px 20px;
		}
	}


	#menu-mobile{
		display: none;
		position: fixed;
		top: 70px;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 9999999999;
		background-color: $color1;
		&.servicios{
			background-color: $color2;
		}
		&.casos{
			background-color: $color7;
		}
		&.productos{
			background-color: $color1;
		}
		&.contacto{
			background-color: $color2;
		}
		&.nosotros{
			background-color: $color4;
		}
		.navbar-nav{
			width: 200px;
			margin: 50px auto 0;
			li{
				text-align: center;
				a.nav-link{
					color: #fff;
					text-transform: uppercase;
					padding: 1rem;
				}
			}
		}
	}
	@media(max-width: 767px){
		.navbar.contacto.active, .navbar.contacto.bg{
			background-color: $color2;
		}
		.foot-c{
			display: flex;
			.foot-c1{
				order: 1;
			}
			.foot-c2{
				order: 2;
			}
			.foot-c3{
				order: 3;
			}
		}

	}

	.section-404{
		height: 84vh;
		background: url(/assets/img/bg-404.jpg) no-repeat center center;
		background-size: cover;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: #fff;
		h1{
			font-size: 130px;
			font-weight: bold;

